@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;500;700&display=swap');

@font-face {
  font-family: 'kardust';
  font-style: normal;
  font-weight: normal;
  src: local('karsust'),
    url('./assets/fonts/kardust/Kardust.otf') format('opentype');
}

@font-face {
  font-family: 'kardust';
  font-style: normal;
  font-weight: bold;
  src: local('karsust'),
    url('./assets/fonts/kardust/Kardust\ Bold.otf') format('opentype');
}

@font-face {
  font-family: 'kardust';
  font-style: italic;
  font-weight: normal;
  src: local('karsust'),
    url('./assets/fonts/kardust/Kardust\ Italic.otf') format('opentype');
}

@font-face {
  font-family: 'kardust';
  font-style: italic;
  font-weight: bold;
  src: local('karsust'),
    url('./assets/fonts/kardust/Kardust\ Bold\ italic.otf') format('opentype');
}

@font-face {
  font-family: 'kardust-extended';
  src: local('karsust-extended'),
    url('./assets/fonts/kardust/Kardust\ Expanded\ Bold\ Italic.otf')
      format('opentype');
}

body {
  margin: 0;
  font-family: 'Inter', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  background: linear-gradient(15deg, rgba(0, 0, 0, 0.2) 10%, transparent 10%),
    linear-gradient(20deg, transparent 75%, rgba(0, 0, 0, 0.2) 75%),
    linear-gradient(
      176deg,
      transparent 50%,
      rgba(255, 255, 255, 0.05) 50%,
      transparent 100%
    ),
    radial-gradient(
      circle,
      #bc5297 0%,
      rgba(134, 45, 165, 1) 50%,
      rgba(24, 86, 144, 1) 75%
    );
}

body::after {
  background: url(data:image/png;base64,);
  content: '';
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
}

code {
  font-family: 'kardust', sans-serif;
}
